<template>
    <div>
        <v-app-bar id="appBar" app dark color="primary">
            <v-app-bar-nav-icon @click="backOrGoToHome">
                <v-icon>mdi-arrow-left</v-icon>
            </v-app-bar-nav-icon>
            <v-text-field prepend-inner-icon="mdi-magnify" hide-details single-line clearable
                          autofocus v-model="searchString"/>
        </v-app-bar>
        <v-container fluid>
            <EmptyArticles v-if="searchArticles.length === 0" tip-string="未搜索到相关文章┑(￣Д ￣)┍"/>
            <ArticlePreview v-else @transferCurrentPage="getCurrentPage" :show-type-and-tags="true"
                            :total-page="searchTotalPage" :blog-articles="searchArticles" :loading="searchLoading"/>
        </v-container>
    </div>
</template>

<script>
    import ArticlePreview from "@/components/ArticlePreview";
    import EmptyArticles from "@/components/constant/EmptyArticles";

    export default {
        name: "SearchArticle",
        components: {EmptyArticles, ArticlePreview},
        props: ["string"],
        data: () => {
            return {
                searchString: "",
                searchTotalPage: 0,
                searchArticles: [],
                searchLoading: false
            }
        },
        mounted() {
            this.searchString = this.string
        },
        methods: {
            //后退或者回到首页
            backOrGoToHome() {
                if (window.history.length <= 1) {
                    this.$router.push({path: '/'})
                    // return false
                } else {
                    this.$router.back()
                }
            },
            getCurrentPage(currentPage) {
                this.getArticles(currentPage)
            },
            getArticles(currentPage) {
                this.axios.get(this.API.URL.SEARCH_ARTICLES + "&page=" + (currentPage - 1) + "&string=" + this.searchString)
                    .then(value => {
                        let data = value.data.data
                        this.searchArticles = data.data
                        this.searchTotalPage = data.totalPages
                        this.searchLoading = false
                    }, () => {
                        this.searchLoading = false
                    })
            }
        },
        watch: {
            searchString() {
                if (this.searchString !== null && this.searchString !== "") {
                    this.searchLoading = true
                    this.getArticles(1)
                }
            }
        }
    }
</script>

